.homeContainer {
  width: 70%;
  height: 90%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap-reverse;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;

  .intro {
    width: 30%;
    font-size: 1rem;
    min-width: 300px;
    margin-bottom: 100px;
    font-weight: 500;
    letter-spacing: 0.19em;

    .cabecalho {
      color: black;
      font-family: var(--roboto);
      font-style: normal;

      padding: 0;

      font-weight: 300;
      font-size: 3rem;
      letter-spacing: 0.245em;
      height: 100px;
    }

    .textBtn {
      font-family: var(--slab);
      font-style: normal;
      font-weight: normal;
      font-size: 1rem;
      line-height: 33px;
      align-items: center;
      text-decoration: none;
      list-style-type: none;

      display: flex;
      padding-left: 15px;
      color: var(--white);
      margin-top: 32px;

      width: 240px;
      height: 53px;
      background-color: rgba(var(--blueRgb), 1);
      border-radius: 10px 30px;
      transition: 0.3s;
      cursor: pointer;

      &:hover {
        transform: translate(-2px, -5px);
        filter: grayscale(10%);
        box-shadow: 6px 7px 6px rgba(var(--blueRgb), 0.4);
      }
    }
  }

  .monitor {
    width: 60%;
    height: 100%;
    position: relative;
  }
  #monitorImg {
    position: absolute;
    top: 50px;
  }
}
@media (max-width: 1920px) {
  .homeContainer{
    width: 90%
  }
}

@media (max-width: 1600px) {
  .intro{
    width: 38%;
    font-size: 15px;
  }
    #monitorImg {
      position: absolute;
      top: 50% !important;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 900px;
    }
}
@media (max-width: 1370px) {
  .intro {
    width: 50%;
    margin-bottom: 0 !important;
  }

  #monitorImg {
    position: absolute;
    top: 50% !important;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
  }
}

